body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  width: 90%;
  text-align: justify;
  background-color: tan;
}

h1 {
  color: black;
  text-align: center;
}

h3, h4 {
  margin-bottom: 0;
}

nav {
  border-bottom: 4px solid black;
  font-family: Univers LT Std;
  text-align: center;
  border-bottom: 1px solid #888;
  font-weight: normal;
  line-height: 23px;
  font-size: 16px;
  position: fixed;
  top: 0;
  width: 100%;
  background: firebrick;
}

em {
  color: white;
}

.text {
  font-size: 20px;
  padding-left: 20px;
}

/* img {
border-radius: 50%;
max-width: 100%
} */

.image {
  flex-basis: 40%
}

nav hidden {
  top: -50px;
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav li {
  display: inline-block;
  padding-top: 16px;
  padding-bottom: 16px;
}

nav a {
  text-decoration: none;
  color: black;
  font-weight: bold;
  padding: 16px 32px;
}

nav a:hover {
  background: black;
  color: white;
}

#page-body {
  max-width: 700px;
  margin: auto;
  padding-top: 64px;
  width: 70%;
  word-wrap: break-word;
}

.article-list-item {
  text-decoration: none;
  color: black;
  padding: 16px 0 0 0;
}

.article-list-item p {
  padding-bottom: 32px;
  border-bottom: 2px solid black;
  /* font-family: Roboto; */
}

#add-comment-form {
  border-bottom: 2px solid black;
  padding: 16px;
}

#add-comment-form h3 {
  margin-bottom: 16px;
}

#add-comment-form input, textarea {
  display: block;
  width: 300px;
  padding: 8px;
  border-radius: 8px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  border: 2px solid black;
  margin-bottom: 16px;
}

#add-comment-form button {
  width: 320px;
}

#upvotes-section {
  display: inline-block;
  margin-right: 16px;
}

#upvotes-section:hover {
  background-color: green;
  color: white;
}

#upvotes-section:disabled{
  pointer-events: none;
}

#downvote-section:disabled{
  pointer-events: none;
}

#downvote-section {
  display: inline-block;
  margin-right: 16px;
}

#downvote-section:hover {
  background-color:rgb(150, 6, 6);
  color: white;
}


button {
  display: inline-block;
  height: 38px;
  padding: 0 30px;
  color: white;
  text-align: center;
  font-size: 11px;
  font-weight: 700;
  line-height: 38px;
  letter-spacing: .1rem;
  text-transform: uppercase;
  text-decoration: none;
  white-space: nowrap;
  border-radius: 2px;
  background-color:black;
  border: 1px solid #331550;
  cursor: pointer;
  box-sizing: border-box;
  border-radius: 10000%;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.comment {
  border-bottom: 2px solid black;
  padding: 16px;
}

.leftpane{
  width: 25%;
  min-width: 350px;
  height: available;
  min-height: 400px;
  float: left;
  background-color: rosybrown;
  border-collapse: collapse;
}

.middlepane{
  width: 50%;
  min-width: 800px;
  height: available;
  min-height: 650px;
  float: left;
  background-color: royalblue;
  border-collapse: collapse;
}

div {
  display: block;
}

.fauxborder-left {
  background-position: top left;
  background-repeat: repeat-y;
  position: relative;
  _height: 1%;
}

a.social {
  margin: 0 1rem;
  transition: transform 250ms;
  display: inline-block;
}

a.social:hover {
  transform: translateY(-2px);
}

a.facebook {
  color: #4968ad;
}

a.twitter {
  color: #49a1eb;
}

a.instagram {
  color: black;
}

a.linkedin {
  color: #2867B2;
}

a.quora {
  color: #a62100;
}

a.github {
  color: black;
}

/* Contact Us CSS */
body {
  font-family: Tahoma, sans-serif;
  margin: 0;
}

.ContactView-title {
  font-size: 24px;
  padding: 0 24px;
}

.ContactView-list {
  list-style: none;
  margin: 0;
  padding: 0;
  border-top: 1px solid #f0f0f0;
}

.ContactItem {
  margin: 0;
  padding: 8px 24px;
  border-bottom: 1px solid #f0f0f0;
}
.ContactItem-name {
  font-size: 16px;
  font-weight: bold;
  margin: 0;
}
.ContactItem-email {
  font-size: 14px;
  margin-top: 4px;
  font-style: italic;
  color: #888;
}
.ContactItem-description {
  font-size: 14px;
  margin-top: 4px;
}


.ContactForm {
  padding: 8px 24px;
}
.ContactForm > input,
.ContactForm > textarea {
  display: block;
  width: 240px;
  padding: 4px 8px;
  margin-bottom: 8px;
  border-radius: 3px;
  border: 1px solid #888;
  font-size: 14px;
}
.ContactForm > input.ContactForm-error {
  border-color: #b30e2f;
}